@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');

:root {
  --color-primary: #16ABF8;
  --color-white: #E5E5E5;
  --color-black: #111111;
}

* {
  font-family: 'Poppins', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

header {
  background-color: var(--color-primary);
  color: white;
  width: 100%;
  padding: 15px;
}

.logo-wrapper {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

h1 {
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
}

main {
  padding: 15px;
}

.heading-wrapper {
  display: flex;
  width: 100%;
  max-width: 1000px;
  justify-content: space-between;
  margin: 43px auto 55px auto;
}

.heading-button {
  padding: 13.5px 29px;
  background-color: var(--color-primary);
  color: white;
  display: flex;
  align-items: center;
  border-radius: 30px;
  border: none;
}

.content-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.activity {
  gap: 26px 20px;
}

.list {
  row-gap: 10px;
}

.card {
  width: 235px;
  height: 234px;
  background: #FFFFFF;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
  padding: 26px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  cursor: pointer;
}

.card-bottom {
  display: flex;
  justify-content: space-between;
}

.icon {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.heading-left, .heading-right {
  display: flex;
  column-gap: 19px;
  align-items: center;
}

/* LIST ITEM */

.list-item {
  width: 100%;
  height: 80px;
  background: #FFFFFF;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  padding: 27px;
}

.list-item input {
  height: 20px;
  width: 20px;
}

.list-item-left {
  display: flex;
  align-items: center;
  column-gap: 16px;
}

.bullet {
  width: 9px;
  height: 9px;
  background-color: #16ABF8;
  border-radius: 5px;
}